import { Layout, Image, Avatar, Row, Col } from "antd";
import "./style.css";
import { HomeTwoTone, AntDesignOutlined } from "@ant-design/icons";
import { Carousel } from "antd";
import Story from "./components/Story";
import Traditional from "./components/Traditional";
import Garden from "./components/Garden";
import Duty from "./components/Duty";
import Reviews from "./components/Reviews";
import Contact from "./components/Contact";
import { Anchor } from "antd";

const { Link } = Anchor;

const { Header, Content, Footer } = Layout;
const contentStyle = {
  height: "400px",
  color: "#fff",
  lineHeight: "160px",
  textAlign: "center",
  background: "#CA965C",
};

const HomePage = () => {
  return (
    <>
      <Layout className="layout">
        <Header style={{ height: "80px"}}>
          <Row style={{ textAlign: "center", fontSize: 20 }}>
            <Col span="4">
              <a className="story" href="#story"> CU CHUYN C PH XA</a>
            </Col>
            <Col span="3">
              <a className="traditional" href="#traditional">C PH XA DN GIAN</a>
            </Col>
            <Col span="3">
              <a className="garden" href="#garden">KHU VN C PH</a>
            </Col>
            <Col span="4">
              <Image
                width={75}
                preview={false}
                className="logo"
                src="/avatar.png"
              />
            </Col>
            <Col span="3">
              <a className="knowledge" href="#knowledge">KIN THC C PH</a>
            </Col>
            <Col span="3">
              <a className="system" href="#system">H THNG</a>
            </Col>
            <Col span="3">
              <a className="contact" href="#contact">LIN H</a>
            </Col>
          </Row>
        </Header>
        <Content>
          <Carousel autoplay>
            <div>
              <h3 style={contentStyle}>1</h3>
            </div>
            <div>
              <h3 style={contentStyle}>2</h3>
            </div>
            <div>
              <h3 style={contentStyle}>3</h3>
            </div>
            <div>
              <h3 style={contentStyle}>4</h3>
            </div>
          </Carousel>

          <Story id="story" />
          <Traditional id="traditional" />
          <Garden id="garden" />
          <Duty id="duty" />
          <Reviews id="reviews" />
          
        </Content>
        <Footer>
          <Contact id="contact" />
        </Footer>
      </Layout>
    </>
  );
};

export default HomePage;
